@use"../function/_mixin.scss" as *;

.cta{
    margin-top: px-clamp(80,120,375,2000);
    &__head{
      font-size: px-clamp(24,36,375,2000);
      font-weight: bold;
      line-height: 1.5;
      text-align: center;
    }
    &__subhead{
        font-size: px-clamp(16,18,375,2000);
        line-height: 1.5;
    }
    &__btn{
      margin-top: px-to-rem(40px);
      text-align: center;
    }
}