@mixin responsive($media) {
  @if $media == "phone" {
    @media (min-width: 768px) {
      @content;
    }
  } @else if $media == "tablet" {
    @media (min-width: 992px) {
      @content;
    }
  } @else if $media == "desktop" {
    @media (min-width: 1100px) {
      @content;
    }
  }
}
